import Vue from 'vue';
import VueRouter from 'vue-router';
import stickyNotes from '../views/stickyNotes.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'all',
    component: stickyNotes,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
